	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import yonghu from '@/views/yonghu/list'
	import jiankangshuju from '@/views/jiankangshuju/list'
	import discusscanyinxinxi from '@/views/discusscanyinxinxi/list'
	import canyinxinxi from '@/views/canyinxinxi/list'
	import yingyangjibie from '@/views/yingyangjibie/list'
	import canyinyingyang from '@/views/canyinyingyang/list'
	import yingyangdengjibiaozhun from '@/views/yingyangdengjibiaozhun/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'
	import users from '@/views/users/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '健康饮食资讯',
			component: news
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/jiankangshuju',
			name: '健康数据',
			component: jiankangshuju
		}
		,{
			path: '/discusscanyinxinxi',
			name: '餐饮信息评论',
			component: discusscanyinxinxi
		}
		,{
			path: '/canyinxinxi',
			name: '餐饮信息',
			component: canyinxinxi
		}
		,{
			path: '/yingyangjibie',
			name: '营养级别',
			component: yingyangjibie
		}
		,{
			path: '/canyinyingyang',
			name: '餐饮营养',
			component: canyinyingyang
		}
		,{
			path: '/yingyangdengjibiaozhun',
			name: '营养等级标准',
			component: yingyangdengjibiaozhun
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
